async function addCardToLocalStorage() {
  function onAClick(e) {
    console.log(' ~ e.target:', e.target);

    if (e.target.nodeName === 'SPAN') {
      console.dir(' ~ e.currentTarget;:', e.currentTarget);
    }
  }
  let card = document.querySelector('.gallery__item');

  card.addEventListener('click', onAClick);
}

